	module.exports = {"data":{"markdownRemark":{"html":"<p>학교 시작,보고. 교양 과목 건물에는 전기가 없었습니다. 모두들 계단을 들고 불평해야했습니다.</p>\n<p>정치 체제 개혁에 관한 영문 기사를 개정하십시오. 어느 오후에 끝내야했다. 이 컴퓨터의 프로그램은 매우 좋으며 중국어 WPS보다 몇 가지 장점이 있으며 의견을 처리 할 수 ​​있습니다. 이것은 중국 소프트웨어에서 가장 어려운 문제입니다. 아마도 기사를 쓰는 중국의 방식과 관련이있을 것입니다. 중국인들은 기사 작성에 익숙했고 재료와 재료를 중요하게 생각하지 않으므로 컴퓨터 소프트웨어에 대한 요구 사항은 없습니다. 상황이 바뀌 었음에도 불구하고 기사에 점점 더 많은 논평이 있으며 요구 사항은 점점 더 높아지고 있습니다. 그러나 나는 시장이 존재하는 한 시장의 힘이 해결 될 수 있다고 믿는다.</p>","id":"/home/swain/workspace/political-life/src/pages/ko/0040.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ko/0040.md","frontmatter":{"title":"2월 23일 수요일"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ko/0040.md absPath of file >>> MarkdownRemark"}}

